<script>
import { ref, onMounted } from 'vue'
import $ from 'jquery'
import Swiper from '../../assets/swiper/swiper-bundle.min.js'
import NoData from "../../components/global/NoData.vue"
import { useRouter } from "vue-router"
import { monthHandle3, errorHandle } from '@/utils/handle'
import { apiGetPinkWorkOrderList } from "@/api/api"

export default {
  setup() {
    const router = useRouter()

    // 頁籤
    function initSwiper(slidesPerView) {
      var tabSwiper = new Swiper('.ui-tab', {
        slidesPerView: slidesPerView,
        freeMode: true,
        watchSlidesVisibility: true,
        watchSlidesProgress: true,
      });

      var pageSwiper = new Swiper('.ui-page', {
        thumbs: {
          swiper: tabSwiper,
        },
      });
    }
    // 頁籤數
    $(function () {
      initSwiper(3);
    });

    let data1 = ref([])
    let data2 = ref([])
    let data3 = ref([])
    let APIparams1 = { status: 1, page: 1, limit: 20 }
    let APIparams2 = { status: 2, page: 1, limit: 20 }
    let APIparams3 = { status: 3, page: 1, limit: 20 }

    const dateToString = (val) => {
      return [val.getFullYear(), val.getMonth() + 1, val.getDate()]
    }

    onMounted(async () => {
      data1.value = await apiGetPinkWorkOrderList(APIparams1)
      data1.value = data1.value.data.list
      data2.value = await apiGetPinkWorkOrderList(APIparams2)
      data2.value = data2.value.data.list
      // data3.value = []
      data3.value = await apiGetPinkWorkOrderList(APIparams3)
      data3.value = data3.value.data.list

      //處理data1的日期
      data1.value.forEach(item => {
        let cancelRes = monthHandle3(item.cancelDate)
        let checkRes = monthHandle3(item.checkDate)
        let requireRes = monthHandle3(item.requireDate)
        item.cancelInfo = cancelRes.dateInfo
        item.checkInfo = checkRes.dateInfo
        item.requireInfo = requireRes.dateInfo

        if (item.startTime == item.endTime) {
          item.timeInfo = `${item.startTime.slice(0, 5)}(24小時)`
        } else {
          item.timeInfo = `${item.startTime.slice(0, 5)}~${item.endTime.slice(0, 5)}`
        }
      })
      // 處理data2的日期
      data2.value.forEach(item => {
        let cancelRes = monthHandle3(item.cancelDate)
        let checkRes = monthHandle3(item.checkDate)
        let requireRes = monthHandle3(item.requireDate)
        item.cancelInfo = cancelRes.dateInfo
        item.checkInfo = checkRes.dateInfo
        item.requireInfo = requireRes.dateInfo
        if (item.startTime == item.endTime) {
          item.timeInfo = `${item.startTime.slice(0, 5)}(24小時)`
        } else {
          item.timeInfo = `${item.startTime.slice(0, 5)}~${item.endTime.slice(0, 5)}`
        }
      })
      //處理data3的日期
      data3.value.forEach(item => {
        let cancelRes = monthHandle3(item.cancelDate)
        let checkRes = monthHandle3(item.checkDate)
        let requireRes = monthHandle3(item.requireDate)
        item.cancelInfo = cancelRes.dateInfo
        item.checkInfo = checkRes.dateInfo
        item.requireInfo = requireRes.dateInfo
        if (item.startTime == item.endTime) {
          item.timeInfo = `${item.startTime.slice(0, 5)}(24小時)`
        } else {
          item.timeInfo = `${item.startTime.slice(0, 5)}~${item.endTime.slice(0, 5)}`
        }
      })
      // console.log("data1", data1.value)
      // console.log("data2", data2.value)
      // console.log("data3", data3.value)

    })

    const goto = (val) => {
      router.push({ path: val })
    }
    return { goto, data1, data2, data3 };
  },
  components: {NoData}
};


</script>

<template>
  <div class="orderList">
    <div class="main">
      <div class="swiper-container ui-tab">
        <div class="swiper-wrapper">
          <div class="swiper-slide"><span>指定邀約</span></div>
          <div class="swiper-slide"><span>主動應徵</span></div>
          <div class="swiper-slide"><span>執行中</span></div>
        </div>
      </div>
      <div class="swiper-container ui-page">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
            <NoData v-if="data1.length == 0" />
            <div class="order-container" v-if="data1.length > 0">
              <div class="card" v-for="item in data1" :key="item.id">
                <div class="card-body">
                  <a @click="goto(`/order/info/${item.workOrderId}/${item.applyId}`)" class="card-link">
                    <div class="card-title">{{ item.name }}({{ item.gender ==
                        'male' ? '男性' : '女性'
                    }}/{{ item.age }}歲/{{ item.height }}cm/{{ item.weight }}kg)</div>
                    <div class="card-location" v-if="item.servicePlace.placeType.id == 1">服務地點 :
                      {{ item.servicePlace.city.name }}{{ item.servicePlace.area.name }}</div>
                    <div class="card-location" v-if="item.servicePlace.placeType.id != 1">服務地點 :
                      {{ item.servicePlace.place.name }}</div>
                    <div class="card-date" v-if="item?.replyStatus == 2">
                      <div class="d-flex">
                        <div class="col-left">接受日期 : </div>
                        <div class="col-right"><span class="yes">{{ item?.checkInfo?.[0] }}</span></div>
                        <div class="col-right" v-if="item?.checkInfo?.[1]"><span class="yes">{{ item?.checkInfo?.[1]
                        }}</span></div>
                      </div>
                      <div class="d-flex">
                        <div class="col-left">取消日期 : </div>
                        <div class="col-right" v-if="(!item?.cancelInfo?.[0])"><span class="no">無</span>
                        </div>
                        <div class="col-right" v-if="(item?.cancelInfo?.[0])"><span class="no">{{ item?.cancelInfo?.[0]
                        }}</span>
                        </div>
                        <div class="col-right" v-if="(item?.cancelInfo?.[1])"><span class="no">{{ item?.cancelInfo?.[1]
                        }}</span>
                        </div>
                      </div>
                    </div>
                    <div class="card-date" v-if="(item?.replyStatus == 0) || (item?.replyStatus == 1)">
                      <div class="d-flex">
                        <div class="col-left">服務日期 : </div>
                        <div class="col-right"><span class="yes">{{ item?.requireInfo?.[0] }}</span></div>
                        <div class="col-right" v-if="item?.requireInfo?.[1]"><span class="yes">{{ item?.requireInfo?.[1]
                        }}</span></div>
                      </div>
                    </div>
                    <div class="card-date" v-if="(item?.replyStatus == 3)">
                      <div class="d-flex">
                        <div class="col-left">服務日期 : </div>
                        <div class="col-right"><span class="no">拒絕邀約</span></div>
                      </div>
                    </div>
                    <div class="card-time">服務時段 : {{ item.timeInfo }}</div>
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div class="swiper-slide">
            <NoData v-if="data2.length == 0" />
            <div class="order-container" v-if="data2.length > 0">
              <div class="card" v-for="item in data2" :key="item.id">
                <div class="card-body">
                  <a @click="goto(`/order/info/${item.workOrderId}/${item.applyId}`)" class="card-link">
                    <div class="card-title">{{ item.name }}({{ item.gender ==
                        'male' ? '男性' : '女性'
                    }}/{{ item.age }}歲/{{ item.height }}cm/{{ item.weight }}kg)</div>
                    <div class="card-location" v-if="item.servicePlace.placeType.id == 1">服務地點 :
                      {{ item.servicePlace.city.name }}{{ item.servicePlace.area.name }}</div>
                    <div class="card-location" v-if="item.servicePlace.placeType.id != 1">服務地點 :
                      {{ item.servicePlace.place.name }}</div>
                    <div class="card-date">
                      <div class="d-flex">
                        <div class="col-left">接受日期 : </div>
                        <div class="col-right"><span class="yes">{{ item?.checkInfo?.[0] }}</span></div>
                        <div class="col-right" v-if="item?.checkInfo?.[1]"><span class="yes">{{ item?.checkInfo?.[1]
                        }}</span></div>
                      </div>
                      <div class="d-flex">
                        <div class="col-left">取消日期 : </div>
                        <div class="col-right" v-if="(!item?.cancelInfo?.[0])"><span class="no">無</span>
                        </div>
                        <div class="col-right" v-if="(item?.cancelInfo?.[0])"><span class="no">{{ item?.cancelInfo?.[0]
                        }}</span>
                        </div>
                        <div class="col-right" v-if="(item?.cancelInfo?.[1])"><span class="no">{{ item?.cancelInfo?.[1]
                        }}</span>
                        </div>
                      </div>
                    </div>
                    <div class="d-flex justify-content-between">
                      <div class="card-time">照顧時段 : {{ item.timeInfo }}</div>
                      <div class="card-rate">回覆率 : <span class="percent">{{ item.completionRate }}%</span></div>
                    </div>
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div class="swiper-slide">
            <NoData v-if="data3.length == 0" />
            <div class="order-container" v-if="data3.length > 0">
              <div class="card" v-for="item in data3" :key="item.id">
                <div class="card-body">
                  <a @click="goto(`/order/info/${item.workOrderId}/${item.applyId}`)" class="card-link">
                    <div class="card-title">{{ item.name }}({{ item.gender ==
                        'male' ? '男性' : '女性'
                    }}/{{ item.age }}歲/{{ item.height }}cm/{{ item.weight }}kg)</div>
                    <div class="card-location" v-if="item.servicePlace.placeType.id == 1">服務地點 :
                      {{ item.servicePlace.city.name }}{{ item.servicePlace.area.name }}</div>
                    <div class="card-location" v-if="item.servicePlace.placeType.id != 1">服務地點 :
                      {{ item.servicePlace.place.name }}</div>
                    <div class="card-date">
                      <div class="d-flex">
                        <div class="col-left">服務日期 : </div>
                        <div class="col-right"><span class="yes">{{ item?.checkInfo?.[0] }}</span></div>
                        <div class="col-right" v-if="item?.checkInfo?.[1]"><span class="yes">{{ item?.checkInfo?.[1]
                        }}</span></div>
                      </div>
                    </div>
                    <div class="d-flex justify-content-between">
                      <div class="card-time">照顧時段 : {{ item.timeInfo }}</div>
                      <div class="card-rate">完成率：<span class="percent">{{ item.completionRate }}%</span></div>
                    </div>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";
@import "@/assets/custom/mixins";
@import "@/assets/swiper/swiper-bundle.min.css";

.orderList {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}
.ui-page{
  height: calc(100vh - $header-height - $footer-height - 15px);
}
</style>